import React, { Component } from "react";

import { Fragment } from "react";
import { MdClose, MdHistory } from "react-icons/md";
import { Redirect } from "react-router-dom";
import ChooseExamCenter, {
  LocationTableInterface,
} from "../Application/ExamCenter/ChooseExamCenter";
import { connect } from "react-redux";
import {
  Auth,
  loadExam,
  examDataInterface,
  PrepareExamsStoreInterface,
  loadExamsList,
  LocationsDataStoreInterface,
  loadAllLocationsData,
  setSelectedLocationData,
  applicationPeriodsStoreInterface,
  loadApplicationPeriods,
  candidateExamStoreInterface,
  setSelectedCandidateExam,
  courseStoreInterface,
  loadCourses,
  setSelectedCourse,
  InitializeExamDetails,
  InitialExamDetails,
  setSelectedPeriod,
  setSelectedPosition,
  positionsStoreInterface,
  loadExamsDone,
} from "../../actions";
import { StoreState } from "../../reducers";
import Loading from "../../components/Loading/Loading";
import {
  ExamInterface,
  examStatus,
  UserExamStatus,
} from "../../interfaces/exam";
import { course, PositionAPI, PositionAPICourse } from "../../interfaces/index";
import {
  applicationExamAPI,
  applicationHistory,
  ApplicationPeriodGet,
  ApplicationStates,
  GetApplicationsAPI,
} from "../../interfaces/application";
import { sentExamInterface } from "../Application/ExamCenter/ExamCenter";
import { BsFileEarmarkText } from "react-icons/bs";
import { IoMdTrash } from "react-icons/io";
import ApplicationHistory from "./ApplicationHistory/ApplicationHistory";
import ExamDetails from "./ExamDetails/ExamDetails";
import { CheckIfExamDone, findTotalMarks } from "../../utils/question_bank";
import PreparedExams from "./ExamStatus/PreparedExams";
import ActiveExams from "./ExamStatus/ActiveExams";
import ExamsDone from "./ExamStatus/ExamsDone";

interface PostExamsProps {
  doExam: examDataInterface;
  auth: Auth;
  loadExam: (user_id: string, exam: ExamInterface) => void;
  selectedPosition: PositionAPI;
  selectedExams: applicationExamAPI[] | null;
  selected_application: string | null;
  updateLocation: (application_id: string, exams: sentExamInterface[]) => void;
  changeLocationError: string | null;
  loading: boolean;
  incrementIncomplete: () => void;
  applicationHistory: applicationHistory[] | undefined;
  deleteApplication: (id: string) => void;
  selectedApplicationDetails: GetApplicationsAPI | null;
  locationsData: LocationsDataStoreInterface;
  loadAllLocationsData: (callback: (loading: boolean) => void) => void;
  setSelectedLocationData: (locationData: LocationTableInterface) => void;
  periods: applicationPeriodsStoreInterface;
  loadApplicationPeriods: (callback: (loading: boolean) => void) => void;
  candidateExam: candidateExamStoreInterface;
  setSelectedCandidateExam: (exam_id: string) => void;
  course: courseStoreInterface;
  loadCourses: (callback: (loading: boolean) => void) => void;
  setSelectedCourse: (course: course | null) => void;
  InitializeExamDetails: (
    periodId: string,
    courseId: string,
    district_id: string,
    center_id: string,
    room_id: string,
    position_id: string,
    callback: (loading: boolean) => void
  ) => void;
  setSelectedPeriod: (period: ApplicationPeriodGet) => void;
  positions: positionsStoreInterface;
  setSelectedPosition: (position: PositionAPI) => void;
  loadExamsDone: (
    periodId: string,
    userId: string,
    callback: (loading: boolean) => void
  ) => void;
  setMaxExamModal: (status: boolean) => void;
  setDisplayDetailsModal: (status: boolean) => void;
}
interface PostExamsState {
  appointmentDetailsModal: {
    status: boolean;
    item: InitialExamDetails | null;
  };
  redirect: boolean;
  displayResults: boolean;
  changeLocation: {
    status: boolean;
    location: ExamInterface | null;
  };
  centersModal: boolean;
  selectedCourse: course | null;
  selectedLocation: applicationExamAPI | null;
  applicationHistoryState: boolean;
  loading: boolean;
  loadingExams: boolean;
  loadingExamsDone: boolean;
}

class _PostExams extends Component<PostExamsProps, PostExamsState> {
  constructor(props: PostExamsProps) {
    super(props);

    this.state = {
      appointmentDetailsModal: {
        status: false,
        item: null,
      },
      redirect: false,
      displayResults: false,
      changeLocation: {
        status: false,
        location: null,
      },
      centersModal: false,
      selectedCourse: null,
      selectedLocation: null,
      applicationHistoryState: false,
      loading: false,
      loadingExams: false,
      loadingExamsDone: true,
    };
  }

  renderExam = (user_id: string, exam: ExamInterface) => {
    exam.status = examStatus.Editing;
    this.props.loadExam(user_id, exam);
    this.setState({ redirect: true });
  };

  closeModal = () => {
    this.setState({ centersModal: false });
    this.props.setMaxExamModal(false);
  };

  updateLocationFunction = (exams: sentExamInterface[]) => {
    if (this.props.selected_application !== null) {
      this.props.updateLocation(this.props.selected_application, exams);
    }
  };

  setLoading = (status: boolean) => {
    this.setState({ loading: status });
  };
  setLoadingExams = (status: boolean) => {
    this.setState({ loadingExams: status });
  };
  setLoadingExamsDone = (status: boolean) => {
    this.setState({ loadingExamsDone: status });
  };

  numberOfIncomplete = (
    exams: sentExamInterface[],
    courses: {
      course_name: string;
      course_code: string;
      description: string;
      school_levels: string[];
      course_id: string;
    }[]
  ) => {
    let count: number = 0;
    for (let i in courses) {
      if (
        exams.filter((item) => item.course_id === courses[i].course_id)
          .length === 0
      ) {
        count = count + 1;
      }
    }
    return count;
  };

  findLocation = (
    course_id: string,
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string
  ) => {
    return this.updateLocationFunction([
      {
        course_id: course_id,
        proctoring_type: "Center",
        center_id: location.center_id,
        district_id: location.district_id,
      },
    ]);
  };

  findExam = (courseId: string) => {
    if (this.props.candidateExam.user_exams_temp !== null) {
      let current_temp = this.props.candidateExam.user_exams_temp.find(
        (itm) => itm.course === courseId
      );
      let testDone = this.props.candidateExam.user_exams_done.find(
        (itm) => itm.course === courseId
      );
      if (
        current_temp !== undefined &&
        (testDone === undefined ||
          testDone.exam_status === UserExamStatus.Editing ||
          testDone.exam_status === UserExamStatus.Paused ||
          testDone.exam_status === UserExamStatus.Done ||
          testDone.exam_status === UserExamStatus.Removed)
      ) {
        return current_temp;
      }
    }
    return null;
  };

  findCourse = (course_id: string): applicationExamAPI | null => {
    if (this.props.selectedExams !== null) {
      let x: applicationExamAPI | null | undefined =
        this.props.selectedExams.find(
          (itm) =>
            this.state.appointmentDetailsModal && itm.course_id === course_id
        );
      if (x !== undefined && x !== null) {
        return x;
      }
    }
    return null;
  };

  pendingCourses = () => {
    let count = 0;
    for (let course of this.props.selectedPosition.courses) {
      if (this.props.candidateExam.user_exams_temp !== null) {
        // let current_temp = this.props.candidateExam.user_exams_temp.find(itm => itm.course === course.course_id);
        let testDone = this.props.candidateExam.user_exams_done.find(
          (itm) => itm.course === course.course_id
        );
        if (testDone === undefined) {
          count = count + 1;
        }
      }
    }
    return count;
  };

  changeLocation = (item: PositionAPICourse) => {
    this.setState({ selectedCourse: item, centersModal: true });
    this.props.setMaxExamModal(true);
  };

  InitializationFn = () => {
    if (
      this.props.periods.selected_period !== null &&
      this.props.selectedPosition !== null
    ) {
      this.props.loadExamsDone(
        this.props.periods.selected_period.application_session_id,
        this.props.auth.user.user_id,
        this.setLoadingExamsDone
      );
      // if (this.state.loadingExamsDone === false) {
      for (let course of this.props.selectedPosition.courses) {
        let testDone = this.props.candidateExam.user_exams_temp.find(
          (itm) => itm.course === course.course_id
        );
        if (testDone === undefined) {
          let center_id = this.props.selectedExams?.find(
            (itm) => itm.course_id === course.course_id
          );
          if (center_id !== undefined) {
            this.props.InitializeExamDetails(
              this.props.periods.selected_period.application_session_id,
              course.course_id,
              center_id.district_id,
              center_id.center_id,
              center_id.room_id,
              this.props.selectedPosition.position_id,
              this.setLoadingExams
            );
          }
        }
      }
      // }
    }
  };

  componentDidMount = () => {
    this.props.loadApplicationPeriods(this.setLoading);
    if (
      this.props.course.courses === null ||
      this.props.course.courses.length === 0
    ) {
      this.props.loadCourses(this.setLoading);
    }
    this.InitializationFn();
    if (this.props.doExam.exam_answers) {
      if (this.props.doExam.exam_answers.status === examStatus.Editing) {
        this.setState({ redirect: true });
      }
    }
  };

  render() {
    if (this.props.candidateExam.selected_exam_id !== null) {
      return <Redirect to="/do-exam" />;
    }
    if (this.state.redirect === true) {
      return <Redirect to="/do-exam" />;
    }
    if (
      this.props.loading === true ||
      this.state.loadingExams === true ||
      this.state.loadingExamsDone === true
    ) {
      return (
        <div className="bg-white border my-4 mx-4 rounded px-4 py-4 flex flex-col">
          <span className="mx-4 animate-pulse">Loading, Please wait...</span>
          <Loading />
        </div>
      );
    }
    return this.props.selectedExams === null ? (
      "No result found!"
    ) : (
      <Fragment>
        {this.state.appointmentDetailsModal.status === true &&
        this.state.appointmentDetailsModal.item !== null ? (
          <>
            <div className="animate__animated animate__fadeInUp animate__faster mt-2 mx-2 rounded-t-md border border-blue-300">
              <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2 mb-2">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Exam:{" "}
                    {this.state.appointmentDetailsModal.item !== null && (
                      <>
                        {
                          this.findCourse(
                            this.state.appointmentDetailsModal.item!.course
                          )?.course_name
                        }
                      </>
                    )}
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() =>
                        this.setState({
                          appointmentDetailsModal: {
                            status: false,
                            item: null,
                          },
                          displayResults: false,
                        })
                      }
                    />
                  </div>
                </div>
              </div>
              {this.findExam(this.state.appointmentDetailsModal.item.course) ===
              null ? (
                <div className="text-yellow-700 font-bold text-center text-3xl bg-yellow-50 border border-yellow-200 rounded px-4 py-4 mx-4 my-4">
                  No exam found for selected course
                </div>
              ) : this.props.periods.applicationPeriods &&
                (this.props.periods.applicationPeriods.find(
                  (itm) =>
                    this.state.appointmentDetailsModal.item &&
                    itm.application_session_id ===
                      this.state.appointmentDetailsModal.item.period
                )?.is_active === false ||
                  this.props.periods.applicationPeriods.find(
                    (itm) =>
                      this.state.appointmentDetailsModal.item &&
                      itm.application_session_id ===
                        this.state.appointmentDetailsModal.item.period
                  )?.is_active === undefined) ? (
                <div className="text-yellow-700 font-bold text-center text-3xl bg-yellow-50 border border-yellow-200 rounded px-4 py-4 mx-4 my-4 flex flex-col items-center">
                  <div>No exam for the selected period</div>
                  <div
                    onClick={() =>
                      this.props.loadApplicationPeriods(this.setLoading)
                    }
                    className="btn bg-white text-gray-700 w-max text-base border border-gray-700 mt-4 cursor-pointer"
                  >
                    Reload
                  </div>
                </div>
              ) : (
                <ExamDetails
                  locationDetails={this.props.selectedExams.find(
                    (itm) =>
                      this.state.appointmentDetailsModal.item &&
                      itm.course_id ===
                        this.state.appointmentDetailsModal.item.course
                  )}
                  examDetails={this.findExam(
                    this.state.appointmentDetailsModal.item.course
                  )}
                  locationsData={this.props.locationsData}
                  loadAllLocationsData={this.props.loadAllLocationsData}
                  setSelectedLocationData={this.props.setSelectedLocationData}
                  periods={this.props.periods}
                  loadApplicationPeriods={this.props.loadApplicationPeriods}
                  candidateExam={this.props.candidateExam}
                  setSelectedCandidateExam={this.props.setSelectedCandidateExam}
                />
              )}
            </div>
          </>
        ) : this.state.centersModal === true &&
          this.state.selectedCourse !== null ? (
          <div className="animate__animated animate__fadeInUp animate__faster mt-2 mx-2 rounded-t-md border border-blue-300">
            <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2 mb-2">
              <div className="flex flex-col w-full">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Change exam location
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() => {
                        this.setState({
                          centersModal: false,
                          selectedCourse: null,
                        });
                      }}
                    />
                  </div>
                </div>
              </div>
            </div>
            <ChooseExamCenter
              positionCourses={this.state.selectedCourse}
              addLocation={this.findLocation}
              closeModal={this.closeModal}
            />
          </div>
        ) : this.state.applicationHistoryState === true ? (
          this.props.applicationHistory === undefined ? (
            <div className="text-yellow-700 font-bold text-2xl text-center">
              No application history found!
            </div>
          ) : (
            <div className="animate__animated animate__fadeInUp animate__faster bg-white mt-2 mx-2 rounded-t-md border border-blue-300">
              <div className="flex flex-row gap-4 items-center justify-between bg-blue-100 py-2 px-2">
                <div className="flex flex-row gap-2 items-center justify-between w-full pl-2">
                  <span className="font-bold text-xl text-gray-800">
                    Application history
                  </span>
                  <div className="bg-white hover:text-yellow-700 hover:bg-white rounded-full p-1 h-10 w-10 border-2 border-white hover:border-yellow-600 flex items-center justify-center">
                    <MdClose
                      className="cursor-pointer font-extrabold text-3xl"
                      onClick={() =>
                        this.setState({ applicationHistoryState: false })
                      }
                    />
                  </div>
                </div>
              </div>
              <ApplicationHistory
                applicationHistory={this.props.applicationHistory}
              />
            </div>
          )
        ) : (
          <div className="">
            <div className="px-2 md:py-2">
              <div className="flex flex-col md:flex-row justify-between items-center md:items-center">
                <div className="flex flex-row items-center gap-3 mb-3 pt-4 lg:pt-0">
                  <span className="bg-blue-50  text-blue-700 font-bold rounded-md px-3 py-1 mt-0">
                    Status:{" "}
                    {this.props.selectedApplicationDetails?.application_status}
                  </span>
                  <div
                    onClick={() =>
                      this.setState({ applicationHistoryState: true })
                    }
                    className="btn bg-gray-100 text-gray-600 border border-gray-300 text-sm flex flex-row items-center gap-2 cursor-pointer hover:border-blue-500 hover:bg-blue-50 hover:text-blue-600 md:mt-0"
                  >
                    <MdHistory className="text-xl" />
                    <span>View application history</span>
                  </div>
                </div>
                <div
                  className={`grid ${
                    this.pendingCourses() > 0 ? "grid-cols-3" : "grid-cols-2"
                  } gap-4`}
                >
                  <div className="col-span-1 flex justify-start md:justify-end">
                    <div className="flex flex-row items-center">
                      <div className="flex flex-col">
                        <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">
                          Total
                        </span>
                        <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">
                          {this.props.selectedExams.length}
                        </span>
                      </div>
                    </div>
                  </div>
                  {this.pendingCourses() > 0 && (
                    <div className="col-span-1 flex justify-start md:justify-end">
                      <div className="flex flex-row items-center">
                        <div className="flex flex-col">
                          <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">
                            Pending
                          </span>
                          <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">
                            {this.props.selectedPosition !== null &&
                              this.pendingCourses()}
                          </span>
                        </div>
                      </div>
                    </div>
                  )}
                  <div className="col-span-1 flex justify-start md:justify-end">
                    <div className="flex flex-row items-center">
                      <div className="flex flex-col">
                        <span className="text-xs md:text-md text-gray-500 font-bold text-left md:text-right">
                          Total marks
                        </span>
                        {
                          // this.state.loadingExams === true || this.state.loadingExamsDone === true ? <span>Wait...</span> :
                          <span className="text-xl md:text-xl font-bold text-gray-700 text-left md:text-right">
                            {(
                              parseInt(
                                findTotalMarks(
                                  this.props.selectedPosition.courses,
                                  this.props.candidateExam.user_exams_done
                                )
                              ) / this.props.selectedPosition.courses.length
                            ).toFixed(1)}
                            %
                          </span>
                        }
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <div className="font-bold flex flex-col md:flex-row items-start md:items-center justify-start md:justify-between gap-2">
                {this.props.selectedApplicationDetails &&
                this.props.selectedApplicationDetails.application_status !==
                  ApplicationStates.Rejected ? (
                  <div className="text-xl flex flex-row items-center gap-2">
                    <BsFileEarmarkText className="text-2xl" />
                    <span>Exams list: </span>
                  </div>
                ) : (
                  <div></div>
                )}
                {this.state.loadingExams === false &&
                  this.state.loadingExamsDone === false &&
                  CheckIfExamDone(
                    this.props.selectedPosition.courses,
                    this.props.candidateExam.user_exams_done
                  ) === false && (
                    <div
                      onClick={() =>
                        this.props.selected_application &&
                        this.props.deleteApplication(
                          this.props.selected_application
                        )
                      }
                      className="btn flex flex-row gap-2 items-center text-red-700 bg-red-50 cursor-pointer hover:bg-red-100 hover:text-red-900"
                    >
                      <IoMdTrash className="text-xl " />
                      <span className="text-sm">Remove application</span>
                    </div>
                  )}
              </div>
            </div>
            {this.props.selectedApplicationDetails &&
              this.props.selectedApplicationDetails.application_status !==
                ApplicationStates.Rejected && (
                <>
                  <ActiveExams
                    selectedPosition={this.props.selectedPosition}
                    selectedExams={this.props.selectedExams}
                    findExam={this.findExam}
                    loadingExams={this.state.loadingExams}
                    loadingExamsDone={this.state.loadingExamsDone}
                    setMaxExamModal={this.props.setMaxExamModal}
                    course={this.props.course}
                    setSelectedCourse={this.props.setSelectedCourse}
                    candidateExam={this.props.candidateExam}
                    positions={this.props.positions}
                    periods={this.props.periods}
                    setSelectedPosition={this.props.setSelectedPosition}
                    setSelectedCandidateExam={
                      this.props.setSelectedCandidateExam
                    }
                    selectedApplicationDetails={
                      this.props.selectedApplicationDetails
                    }
                  />
                  <PreparedExams
                    selectedPosition={this.props.selectedPosition}
                    selectedExams={this.props.selectedExams}
                    findExam={this.findExam}
                    loadingExams={this.state.loadingExams}
                    loadingExamsDone={this.state.loadingExamsDone}
                    setMaxExamModal={this.props.setMaxExamModal}
                    course={this.props.course}
                    setSelectedCourse={this.props.setSelectedCourse}
                    candidateExam={this.props.candidateExam}
                    positions={this.props.positions}
                    periods={this.props.periods}
                    setSelectedPosition={this.props.setSelectedPosition}
                    setSelectedCandidateExam={
                      this.props.setSelectedCandidateExam
                    }
                    changeLocation={this.changeLocation}
                    selectedApplicationDetails={
                      this.props.selectedApplicationDetails
                    }
                  />
                  <ExamsDone
                    selectedPosition={this.props.selectedPosition}
                    selectedExams={this.props.selectedExams}
                    findExam={this.findExam}
                    loadingExams={this.state.loadingExams}
                    loadingExamsDone={this.state.loadingExamsDone}
                    setMaxExamModal={this.props.setMaxExamModal}
                    course={this.props.course}
                    setSelectedCourse={this.props.setSelectedCourse}
                    candidateExam={this.props.candidateExam}
                    positions={this.props.positions}
                    periods={this.props.periods}
                    setSelectedPosition={this.props.setSelectedPosition}
                    setSelectedCandidateExam={
                      this.props.setSelectedCandidateExam
                    }
                  />
                </>
              )}
          </div>
        )}
      </Fragment>
    );
  }
}

const mapStateToProps = ({
  auth,
  doExam,
  prepareExam,
  locationsData,
  periods,
  candidateExam,
  course,
  positions,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  prepareExam: PrepareExamsStoreInterface;
  locationsData: LocationsDataStoreInterface;
  periods: applicationPeriodsStoreInterface;
  candidateExam: candidateExamStoreInterface;
  course: courseStoreInterface;
  positions: positionsStoreInterface;
} => {
  return {
    auth: auth,
    doExam: doExam,
    prepareExam: prepareExam,
    locationsData: locationsData,
    periods: periods,
    candidateExam: candidateExam,
    course: course,
    positions: positions,
  };
};

const PostExams = connect(mapStateToProps, {
  loadExam,
  loadExamsList,
  loadAllLocationsData,
  setSelectedLocationData,
  loadApplicationPeriods,
  setSelectedCandidateExam,
  loadCourses,
  setSelectedCourse,
  InitializeExamDetails,
  setSelectedPeriod,
  setSelectedPosition,
  loadExamsDone,
})(_PostExams);

export default PostExams;
